header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 90vh;
  background: var(--clr-light);
  box-shadow: var(--bs);
}

.nav {
  position: fixed;
  background: var(--clr-dark);
  color: var(--clr-light);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;

  transform: translateX(100%);
  transition: transform 250ms cubic-bezier(0.5, 0, 0.5, 1);
}

.nav__list {
  list-style: none;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav__link {
  color: inherit;
  font-weight: var(--fw-bold);
  font-size: var(--fs-h2);
  text-decoration: none;
}

.nav__link:hover {
  color: var(--clr-accent);
}

.nav-toggle {
  padding: 0.5em;
  background: transparent;
  border: 0;
  cursor: pointer;
  position: absolute;
  right: 1em;
  top: 2em;
  z-index: 1000;
}

.nav-open .nav {
  transform: translateX(0);
}

.nav-open .nav-toggle {
  position: fixed;
}

.nav-open .hamburger {
  transform: rotate(0.625turn);
}

.nav-open .hamburger::before {
  transform: rotate(90deg) translateX(-6px);
}

.nav-open .hamburger::after {
  opacity: 0;
}

.hamburger {
  display: block;
  position: relative;
}

.hamburger,
.hamburger::before,
.hamburger::after {
  background: var(--clr-accent);
  width: 2em;
  height: 3px;
  border-radius: 1em;
  transition: transform 250ms ease-in-out;
}

.hamburger::before,
.hamburger::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
}

.hamburger::before {
  top: 6px;
}
.hamburger::after {
  bottom: 6px;
}
.cart {
  cursor: pointer;
  position: absolute;
  right: 3em;
  top: 0.5em;
  font-size: 1.5rem;
}

@media (min-width: 50rem) {
  header {
    bottom: 85vh;
  }
  .nav-open .nav {
    transform: translateX(60%);
  }
  .nav__list {
    align-items: flex-start;
    padding-left: 1em;
    margin: 1em;
  }
  .nav-toggle {
    top: 2em;
    right: 2em;
    font-size: var(--fs-h3);
  }

  .hamburger::before {
    top: 10px;
  }
  .nav-open .hamburger::before {
    transform: rotate(90deg) translateX(-10px);
  }
  .hamburger::after {
    bottom: 10px;
  }
  .cart {
    right: 5em;
    top: 1.25em;
  }
}
